.wrapper {
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}
.inner {
    background: white;
    border: 1px solid #777;
    padding: 50px 40px 40px;
border-radius: 16px;
position: relative;
}
.icon {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
}